input[type=text] {
  width: 100%;
  border: 2px solid #aaa;
  border-radius: 4px;
  margin: 2px 0;
  outline: none;
  padding: 8px;
  box-sizing: border-box;
  transition: .3s
}

input[type=text]:focus {
  border-color: dodgerBlue;
  boz-shadow: 0 0 8px 0 dodgerBlue;
}

